html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body {
  font: 1.6rem Helvetica;
  height: 100vh;
  margin: 0;

}
button{
  font-size: 1.6rem;
}
.full-width{
  width: 100%;
}
#root{
  height: 100%;
}
.grid-container {
  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
.header {
  grid-area: header;
  background-color: black;
  color: aliceblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.brand a {
  color: #ffff;
  font-size: 2.5rem;
  font-weight: bold;
  text-decoration: none;
}
.header-links a {
  color: #ffff;
  text-decoration: none;
  font-size: 2rem;
  padding: 1rem;
}
.header-links a:hover {
  color: brown;
}
.main {
  grid-area: main;
}
.footer {
  grid-area: footer;
  background-color: black;
  color: aliceblue;
  display: flex;
  justify-content: center;
  align-items: center; 
  
}
.products {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.products li {
  list-style-type: none;
  padding: 0;
  flex: 0 1 34rem;
  margin: 1rem;
  height: 50rem;
  border-bottom: 0.1rem #c0c0c0 solid;
}
.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.product-name {
  font-size: 2rem;
  font-weight: bold;
}
.product-name a {
  text-decoration: none;
}
.product-name a:hover {
  color: brown;
}
.product-brand {
  font-size: 1.9rem;
  color: blueviolet;
}
.price {
  font-size: 2rem;
  font-weight: bold;
}
.product-image {
  max-width: 34rem;
  max-height: 34rem;
}
.product-rating {
  margin-bottom: 1rem;
}

.brand button {
  font-size: 3rem;
  padding: 0.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}
.sidebar {
  position: fixed;
  transition: all .5s;
  transform: translateX(-40rem);
  width: 30rem;
  background-color: white;
  height: 100%;
}
.sidebar.open {
  transform: translateX(0);
}
.sidebar-close-button {
  border-radius: 50%;
  border: 0.1rem black solid;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem;
  cursor: pointer;
  position: absolute;
  right: 0.5rem;
  top: 1.6rem;
}

.details{
  display: flex;
  justify-content: space-between;
  align-items: flex-start; 
  flex-wrap: wrap;
  padding: 1rem;
}
.details-image{
   flex : 1 1 60rem
}
.details-image img{
  max-width: 60rem;
  widows: 100%;
}
.details-info{
  flex : 1 1 30rem

}
.details-action{
  flex : 1 1 30rem;
  border:.1rem #808088 solid;
  border-radius: 0.5rem;
  background-color: #f0f0f0;
  padding: 1rem;
} 
.details-info ul{
  list-style-type: none;
  padding: 0;
}
 .details-action ul{
  list-style-type: none;
  padding: 0;
}
.details-info li{
margin-bottom: 1rem;
}
 .details-action li{
  margin-bottom: 1rem;}

.back{
  padding: 1rem;
}
.backlink{
  text-decoration: none;
}
.backlink:hover{
  color: brown;
}
.details-action ul li:last-child{
  display: flex;
  flex-direction: column;
}
.button{
  padding: 1rem;
  border : .1rem #808080 solid;
  border-radius: 0.5rem;
  cursor: pointer;
  background-color: orange;
}
.button:hover{
  border: .1rem #440044 solid;
}
.button.primary{
  background-color: orange;
}
.button.secondary{
  background-color: #f0f0f0;
}
.text-center{
  text-align: center;
}
.cart{
 display: flex;
  flex-wrap: wrap;
  margin: 1rem;
  align-items: flex-start;
}
.cart-list{
  flex:3 1 60rem;
}
.cart-action{
  flex:1 1 20rem;
  background-color: #f8f8f8;
  border : .1rem #808088 solid;
  border-radius: .5rem;
  padding: 1rem;
}
.cart-list-container{
  padding: 0;
  list-style: none;
  padding: 1rem;

}
.cart-list-container li{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 1rem ;
  border-bottom:.1rem #808080 solid ;
 }
.cart-list-container img{
 max-width: 10rem;
 max-height: 10rem;


}
.cart-list-container li:first-child{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.cart-image{
 flex: 1 1;
}
.cart-name{
  flex: 8 1;
 }
 .cart-price{
  flex: 1 1;
 font-size: 2.5rem;
 text-align: right;
 text-decoration: none;
 }
 .link{
   text-decoration: none;
 }
 .link:hover{
   color: darkgray;
 }
 .form {
   display: flex;
   justify-content: center;
   align-items: center;
   
 }
 .form-container{
  display: flex;
  flex-direction: column;
  width:32rem ;
  padding: 2rem;
  border: .1rem #c0c0c0 solid;
  border-radius:.5rem ;
  list-style: none;
 }
 .form-container li{
  display: flex;
  flex-direction: column;
  margin-bottom:1rem ;
  margin-top: 1rem;

 }
 input{
   padding: 1rem;
   border: .1rem #c0c0c0 solid;
   border-radius: .5rem;  
 }
 .product-header{
   display: flex;
   justify-content: space-between;
   align-items: flex-start ;
 }
 .content-margined{
     margin: 1rem;
 }
 .table{
   width: 100%;

 }
 th{
   text-align: left;

 }
 tbody>tr:nth-child(odd){
   background-color: #c0c0c0;
 }

 .checkout-steps{
   display: flex;
   justify-content: space-between;
   width: 40rem;
   margin: 1rem auto;
 }
 .checkout-steps > div{
   border-top:.3rem #c0c0c0 solid;
   color : #c0c0c0;
   flex: 1 1;
   padding-top:1rem ;
 }
 .checkout-steps > div.active{
  border-top:.3rem #f08000 solid;
  color : #f08000;
  
}
.placeorder{
  display: flex;
  flex-grow: wrap;
  padding: 1rem;
  justify-content: space-between;

}
.placeorder-info{
    flex: 3 1 60rem;
}
.placeorder-action{
  flex: 3 1 60rem;
  border: .1rem #c0c0c0;
  border-radius: .5rem;
  background-color: #c0c0c0;
  padding: 1rem;
}
.placeorder-info >div{
  border: .1rem #c0c0c0;
  border-radius: .5rem;
  background-color:#c0c0c0;
  padding: 1rem;
  margin: 1rem;
}
.placeorder-info >div:first-child{
margin-top:0 ;
}
.placeorder-action >ul {
  padding: 0;
  list-style: none;

}
.placeorder-action>ul >li{
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.placeorder-action>ul >li:last-child{
font-size: 2rem;
color: #c04000 ;
font-weight: bold;
}
.profile {
  display: flex;
  flex-wrap: wrap;
}

.profile-info {
  flex: 1 1 30rem;
}
.profile-orders {
  flex: 3 1 60rem;
}

/* admin */
.dropdown {
  display: inline-block;
  position: relative;
}
.dropdown-content {
  position: absolute;
  display: none;
  right: 0;
  padding: 1rem;
  list-style-type: none;
  z-index: 1;
  background-color: #203040;
  margin: 0;
  margin-top: 0.4rem;
}
.dropdown:hover .dropdown-content {
  display: block;
}

/* filter */

.filter {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 1rem auto;
  max-width: 45rem;
  justify-content: space-between;
  align-items: center;
}
.filter input,
.filter button,
.filter select {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #c0c0c0 solid;
  font-size: 1.6rem;
}
.categories {
  padding: 0;
  list-style-type: none;
}
.categories a {
  display: flex;
  padding: 1rem;
}

.categories a:hover {
  background-color: #c0c0c0;
}